const isValidIp = (value: string) => !!/^(?:(?:^|\.)(?:2(?:5[0-5]|[0-4]\d)|1?\d?\d)){4}$/.test(value)

const useQueryParams = () => {
    const [queryParams, setQueryParams] = useState<{data:string, lang:string, status: string, gl: { interface: 'vpl3' | 'scratch' }}>({ data: "", lang: 'en', status: "", gl: { interface: 'vpl3'}});
  
    useEffect(() => {
      const params = new URLSearchParams(window.location.search);
      const queryParamsObj = { data: "", lang: 'en', status: "", gl: "" };
      params.forEach((value, key) => {
        queryParamsObj[key as keyof typeof queryParamsObj] = value;
      });
      setQueryParams({ data: queryParamsObj.data !== '' ? JSON.parse(queryParamsObj.data) : {}, lang:queryParamsObj.lang , status: queryParamsObj.status, gl:  queryParamsObj.gl !== '' ? JSON.parse(queryParamsObj.gl) : {}});
    }, []);
  
    return {queryParams};
};

