type Interfaz = (nodeId:string,ip:string)=>string

const linkSelector = (_nodeId:string, _ip:string, type: 'vpl3' | 'scratch', nodeName: string, langue:string)=>{
  const selectorInterfaz: { vpl3: Interfaz, scratch: Interfaz } = {
    vpl3: (nodeId:string, ip:string)=> `http://localhost:3000/vpl3/index.html?robot=thymio-tdm&role=teacher&uilanguage=${langue}#uuid=%7B${nodeId}%7D&w=ws://${ip}:8597&pass=8D0SYB&name=${nodeName}`,
    scratch: (nodeId:string, ip:string)=> `http://localhost:3000/scratch/index.html?device=%7B${nodeId}%7D&ws=ws://${ip}:8597&pass=8D0SYB&name=${nodeName}`, 
  }
  return selectorInterfaz[type]( _nodeId, _ip) ?? selectorInterfaz['vpl3']( _nodeId, _ip)
}

const photoSelector = (type: 'vpl3' | 'scratch')=>{
  const selectorPhoto: { vpl3: string, scratch: string } = {
    vpl3: vpl3DescriptionImage,
    scratch: scratchDescriptionImage, 
  }
  return selectorPhoto[type] ?? selectorPhoto['vpl3']
}

