class Nodes {
    nodes: Observable<{ [host:string]:ThymioRobot[]}> = createObservable({
      key: 'all services nodes robots found',
      initialValue: {},
    });

    addNode(node: ThymioRobot[], host: string) {
        this.nodes.set({
            ...this.nodes.state,
            [host]: node,
        })
    }

    onChange = (fun: (args: { [host:string]:ThymioRobot[]}) => void) => {
      subscribe(this.nodes, () => fun(toJS(this.nodes.state)))
    };  
    
    getNodes() {
        return toJS(this.nodes.state)
    }
}

// Definimos el tipo para el estado del hook
interface ScreenState {
  width: number;
  height: number;
  orientation: 'landscape' | 'portrait';
}

// Hook personalizado
function useScreen() {
  // Estado inicial basado en las propiedades actuales de la ventana
  const [screen, setScreen] = useState<ScreenState>({
    width: window.innerWidth,
    height: window.innerHeight,
    orientation: window.innerWidth > window.innerHeight ? 'landscape' : 'portrait',
  });

  useEffect(() => {
    // Función para actualizar el estado basado en las dimensiones actuales de la ventana
    const handleResize = () => {
      setScreen({
        width: window.innerWidth,
        height: window.innerHeight,
        orientation: window.innerWidth > window.innerHeight ? 'landscape' : 'portrait',
      });
    };

    // Agregamos el event listener
    window.addEventListener('resize', handleResize);

    // Limpiamos el event listener al desmontar el componente
    return () => window.removeEventListener('resize', handleResize);
  }, []); // El array vacío asegura que el efecto se ejecute solo una vez al montar

  return screen;
}

const ReloadButton = styled.div`
  cursor: pointer;
  width: calc(100% - 5rem);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 24px;
  margin: 0px;
  background-color: #1976D2;
  color: white;
  border-radius: 4px;
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
  transition: background-color 250ms linear, box-shadow 250ms linear;

  &:hover {
    background-color: #115293;
  }

  &:active {
    box-shadow: none;
  }
`;

const spin = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`;

// Componente Spinner estilizado con animación
const Spinner = styled.div`
  border: 4px solid #fff;
  border-radius: 50%;
  border-top: 4px solid #3498db; // Color del borde superior
  width: 50px; // Ancho del spinner
  height: 50px; // Alto del spinner
  animation: ${spin} 2s linear infinite; // Aplicar la animación de giro
`;
  
function App() {
    const { width, height, orientation } = useScreen();
    const [wsHost, setWsHost] = useState<Array<{host: string, hostName: string, ip: string, addresses: string[]}>>([])
    const {queryParams} = useQueryParams();
    const [allNodes, setAllNodes] = useState<{ [host:string]:ThymioRobot[]}>({});
    const [zeroconfNotifed, setZeroconfNotifed] = useState<any>({})
    const [isWaiting, setIsWaiting] = useState<boolean>(false)

    const nodes = new Nodes()

    const { title, description, list, help, visit, common } = textSelector(queryParams.gl.interface, queryParams.lang)

    const findThymioInLocalNetwork = (data: any) => {
        setZeroconfNotifed(data)
        const tempWsHost = Object.values(data).map(({ txt, addresses }: any) => {
            const ip = addresses.find((_ip: string) => isValidIp(_ip))
            return {
              host: `ws://${ip}:${txt['ws-port']}`,
              hostName: txt['name'],
              ip,
              addresses,
            }
        })
        setWsHost(tempWsHost)
    }

    const reloadScan = async () => {
      if (!isWaiting) { // Verificar si ya se está esperando para ejecutar
        setIsWaiting(true); // Establecer la bandera de espera
    
        setTimeout(() => {
          findThymioInLocalNetwork(zeroconfNotifed); // Ejecutar la función después de 3 segundos
          setIsWaiting(false) // Restablecer la bandera de espera después de la ejecución
        }, 3000); // Retraso de 3 segundos
      } else {
        console.log('a findThymioInLocalNetwork is already in progress');
      }
    }

    useEffect(() => {
      wsHost.forEach(({host, ip, addresses, hostName}) => {
        const connectClient = createClient(host)
        connectClient.onNodesChanged = async (nodesRobots: INode[]) => {
            const _nodes:any = await Promise.all(
              nodesRobots.map(async (node: INode) => {
                    console.log(`Found ${node.name} ${node.id} `)
                      const nodeId = node.id.toString().replace(/[^a-zA-Z0-9 -]/g, '')
                      const { name } = node
                      node.watchSharedVariablesAndEvents(true)

                      node.onGroupChanged = (group: IGroup) => {
                          
                      }

                      node.onStatusChanged = (status) => {
                          
                      }

                      return { nodeId, name, status: node.statusAsString, type: node.typeAsString, ip, addresses, hostName }
                  })
              )

            nodes.addNode(_nodes, host)
          }
      })
    }, [wsHost])

    useEffect(() => {
        if (queryParams.data && queryParams.status !== 'stoped') {
            findThymioInLocalNetwork(queryParams.data)
        }
    }, [queryParams])

    useEffect(() => {
     nodes.onChange((_nodes) => {
        setAllNodes(_nodes)
      })  
    }, [nodes.nodes])

    return (
      <div style={{
        paddingRight: '2rem',
        paddingLeft: '0.5rem',
        paddingBottom: '2rem',
        paddingTop: '3rem',
        backgroundColor: '#271845',
        color: '#fff',
        width: 'auto',
        height: 'calc(100vh - 5rem)',
        display: 'flex',
        overflowX: 'hidden',
        overflowY: 'scroll',
       }}>
           <div style={{ flex: 1, height: '100%', display: 'flex', flexDirection: 'column' }}>
                <img src={photoSelector(queryParams.gl.interface)} style={{ width: '100%', height: 'calc(40% - 3rem)', objectFit: 'cover', paddingLeft: '2rem'}} alt="Descripción de la imagen" />
                <div style={{ padding: '1rem', textAlign: 'center', paddingBottom: '2rem' }}>
                  <p style={{...styles.title, textAlign: 'justify'}}>
                      {title}
                  </p>
                  <p style={{...styles.paragraphe, textAlign: 'justify'}}>
                      {description}
                  </p>
                  <div style={styles.lu}>
                      {list.map((item, index) => (
                        <p key={index} style={{...styles.list, textAlign: 'justify'}}>• {item}</p>
                      ))}
                  </div>
                  <p style={{...styles.paragraphe, textAlign: 'justify'}}>
                      {help}
                  </p>
                  <p style={{...styles.paragraphe, textAlign: 'justify'}}>
                      {visit.split('https://www.thymio.org').map((part, index) =>
                        index === visit.split('https://www.thymio.org').length - 1 ? (
                          <span key={index}>{part}</span>
                        ) : (
                          <React.Fragment key={index}>
                            <span>{part}</span>
                            <span
                              style={{ fontWeight: '400', color: '#0A9EEB', cursor: 'pointer' }}
                              onClick={() => window.open('https://www.thymio.org', '_blank')}
                            >
                              https://www.thymio.org
                            </span>
                          </React.Fragment>
                        )
                      )}
                  </p>
               </div>
           </div>

          <div style={{ flex: 1, maxWidth: '60%', height: '100%', padding: '0px', margin: '0px', alignItems: 'flex-start', justifyContent: 'flex-start'}}>

            <div style={{
                overflowY: 'auto',
                width: '100%',
                display: 'flex',
                flexWrap: 'wrap',
                rowGap: '10px',
                flexDirection: 'row',
                padding: '0 3rem',
            }}>
                { 
                  isWaiting ? (
                    <>
                      <div style={{
                        width: '100%',
                        height: '40vh',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        flexDirection: 'column',
                      }}>
                        <Spinner /> 
                        <br />
                        <div>
                          {common.rescanInfo}
                        </div>
                      </div>
                    </>
                  ):(
                  <>
                    <ReloadButton onClick={reloadScan}>
                      {common.reload_scan}
                    </ReloadButton>
                
                    {
                    Object.values(allNodes).reduce((acc, val) => acc.concat(val), []).map((node: ThymioRobot, index: number) => (
                        <div
                            key={index}
                            style={{
                              display: 'flex',
                              flexDirection: 'column',
                              alignItems: 'center',
                              justifyContent: 'flex-start',
                              margin: '0.2rem', // Margen uniforme alrededor de cada robot
                              fontFamily: 'Roboto',
                              fontSize: '1.2em',
                              maxWidth: '160px', // Establece un ancho máximo para cada robot
                              maxHeight: '160px', // Establece una altura máxima para cada robot
                              flexBasis: '80px', // Establece una base flexible para cada robot
                              opacity: node.status === 'busy' ? 0.3 : 1,
                            }}
                            onClick={()=> {
                              const fullUrl = linkSelector(node.nodeId, node.ip, queryParams.gl.interface, node.name, queryParams.lang)
                              window.open(fullUrl);
                            }}
                        >
                            <Icon name="thymio" sizeH={60} sizeW={100} palleteFill={[...statusColors[node.status] ?? []]} />
                            <div 
                              style={{ width: '140px', textAlign: 'center', fontSize: '1rem', fontWeight: 600,
                              whiteSpace: 'nowrap', /* Asegura que el texto se mantenga en una sola línea */
                              overflow: 'hidden', /* Oculta cualquier texto que exceda el ancho del contenedor */
                              textOverflow: 'ellipsis' /* Añade '...' al final del texto si se corta */
                          }}>{node.name}</div>
                        </div>
                    ))
                    }          
                </>
                )
                }
            </div>
          </div>
       </div>
    )
}

export default App
